.modal {
   width: 100%;
   height: 100%;
   background-color: rgba(0, 0, 0, 0.4);
   position: fixed;
   top: 0;
   left: 0;
   display: flex;
   align-items: center;
   justify-content: center;

   opacity: 0;
   pointer-events: none;
   transition: 0.5s;

   font-family: Arial, Helvetica, sans-serif;
   font-style: normal;
   font-size: 12px;
}

.modal.active {
   opacity: 1;
   pointer-events: all;
}

.modal_content {
   padding: 20px 32px;
   border-radius: 20px;
   background-color: white;

   transform: scale(0.5);
   transition: 0.4s all;
}

.modal_content.active {
   transform: scale(1);
}